import { AxiosResponse } from 'axios';
import { API_ERROR_UNDEFINED } from './axios';

const API_ERROR_GENERIC = 'API Error: "An error has occured. Please try again"';

interface SuccessResponse<ServerData> {
  success: true;
  data: ServerData;
}

type ErrorKeys = 'error' | 'error_message' | 'error_description' | 'message';
type FailedServerData = { [key in ErrorKeys]?: string };

export interface FailedResponse {
  success: false;
  status?: number;
  error: FailedServerData;
}

export type Response<ServerData> = SuccessResponse<ServerData> | FailedResponse;

export const requestAPI = async <ServerData>(
  handler: () => Promise<AxiosResponse<ServerData | FailedServerData> | undefined>
): Promise<Response<ServerData>> => {
  try {
    const result = await handler();
    if (result?.status && result?.status >= 200 && result?.status < 300) {
      return {
        success: true,
        data: result.data as ServerData,
      };
    }

    return {
      success: false,
      status: result?.status,
      error: (result?.data as FailedServerData) || {
        message: API_ERROR_GENERIC,
      },
    };
  } catch (error: any) {
    return {
      success: false,
      error: {
        message: error.message || API_ERROR_UNDEFINED,
      },
    };
  }
};
