// Import the functions you need from the SDKs you need
import { getFirestore, getDoc, doc, setDoc } from 'firebase/firestore';
import { initializeApp } from 'firebase/app';

// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries
export interface DBType {
  mnq: number;
  sqqq: number;
  tqqq: number;
  query_time: { seconds: number; nanoseconds: number };
}

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
  measurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID,
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Cloud Firestore and get a reference to the service
const db = getFirestore(app);

const getData = async (): Promise<DBType | undefined> => {
  const docRef = doc(
    db,
    process.env.REACT_APP_FIREBASE_PROJECT_ID as string,
    process.env.REACT_APP_FIREBASE_DOCUMENT_ID as string
  );
  const docSnap = await getDoc(docRef);
  const data = docSnap.data() as DBType;

  return data;
};

const writeData = async (data: DBType) => {
  const docRef = doc(
    db,
    process.env.REACT_APP_FIREBASE_PROJECT_ID as string,
    process.env.REACT_APP_FIREBASE_DOCUMENT_ID as string
  );
  const result = await setDoc(docRef, data);

  return result;
};

export { db, getData, writeData };
