import Axios from 'lib/axios';
import { YahooFinanceType } from 'types/yahooFinance';
import { DBType } from 'lib/firebase/firebase';
import { MNQConverterContextProps } from 'pages/MNQConverter/MNQConverterContext';

export interface ConversionValueType {
  target: string;
  value: string;
}

const getYahooFinanceData = async (): Promise<DBType | undefined> => {
  const result = await Axios.GET<YahooFinanceType>(
    process.env.REACT_APP_YAHOO_FINANCE_URL as string,
    {
      'X-API-KEY': process.env.REACT_APP_YAHOO_FINANCE_API,
    }
  );

  if (result.success && !result.data.quoteResponse.error) {
    return {
      mnq: result.data.quoteResponse.result[0].regularMarketPrice,
      sqqq: result.data.quoteResponse.result[1].regularMarketPrice,
      tqqq: result.data.quoteResponse.result[2].regularMarketPrice,
      query_time: { seconds: new Date().getTime(), nanoseconds: 0 },
    };
  }

  return undefined;
};

const calculateLeverageValue = (
  isTqqq: boolean,
  nqPrice: number,
  targetNqPrice: number,
  leveragePrice: number
): number =>
  Math.round(
    leveragePrice * (1 - (isTqqq ? -1 : 1) * ((targetNqPrice - nqPrice) / nqPrice) * 3.0) * 100
  ) * 0.01;

const calculateNQValue = (
  isTqqq: boolean,
  nqPrice: number,
  targetLeveragePrice: number,
  leveragePrice: number
): number =>
  (nqPrice * (targetLeveragePrice / leveragePrice - 1)) / (-3.0 * (isTqqq ? -1 : 1)) + nqPrice;

const calculateLeverageArray = (
  toggleTqqqSqqq: MNQConverterContextProps['toggleTqqqSqqq'],
  nqPrice = 0,
  tqqqPrice = 0,
  sqqqPrice = 0,
  offset = 20,
  range = 50
) => {
  let newUpData: ConversionValueType[] = [];
  let newDownData: ConversionValueType[] = [];

  const isTqqq = toggleTqqqSqqq === 'tqqq';
  const leveragePrice = isTqqq ? tqqqPrice : sqqqPrice;

  for (let i = 1; i <= range; ++i) {
    const upValue = nqPrice + offset * i;
    const downValue = nqPrice - offset * i;

    const upPrice = calculateLeverageValue(isTqqq, nqPrice, upValue, leveragePrice).toFixed(2);
    const downPrice = calculateLeverageValue(isTqqq, nqPrice, downValue, leveragePrice).toFixed(2);

    newUpData = [{ target: upValue?.toFixed(2), value: upPrice }, ...newUpData];
    newDownData = [...newDownData, { target: downValue?.toFixed(2), value: downPrice }];
  }

  return [
    ...newUpData,
    {
      target: (nqPrice || 0)?.toFixed(2),
      value: `${((isTqqq ? tqqqPrice : sqqqPrice) || 0)?.toFixed(2)}`,
    },
    ...newDownData,
  ];
};

export { getYahooFinanceData, calculateLeverageValue, calculateLeverageArray, calculateNQValue };
