import { memo, useContext, useCallback } from 'react';
import { ToggleButtonGroup, ToggleButton, Typography } from '@mui/material';

import { TABLE_RANGE } from 'constants/common';
import { calculateLeverageArray } from '../../MNQConverterHelpers';
import MNQConverterContext from '../../MNQConverterContext';
import { MNQConverterContextProps } from '../../MNQConverterContext';

const ToggleTqqqSqqq = () => {
  const {
    toggleTqqqSqqq,
    setToggleTqqqSqqq,
    setLeverageArray,
    nqValue,
    tqqqValue,
    sqqqValue,
    offsetValue,
  } = useContext(MNQConverterContext);

  const calculateLeverge = useCallback(
    (newToggleTqqqSqqq: MNQConverterContextProps['toggleTqqqSqqq']) => {
      setToggleTqqqSqqq(newToggleTqqqSqqq);
      setLeverageArray(
        calculateLeverageArray(
          newToggleTqqqSqqq,
          nqValue,
          tqqqValue,
          sqqqValue,
          offsetValue,
          TABLE_RANGE
        )
      );
    },
    [setToggleTqqqSqqq, setLeverageArray, nqValue, tqqqValue, sqqqValue, offsetValue]
  );

  return (
    <>
      <Typography variant="h6" gutterBottom>
        TQQQ or SQQQ?
      </Typography>
      <ToggleButtonGroup
        color="primary"
        value={toggleTqqqSqqq}
        exclusive
        fullWidth
        onChange={(_e, newAlignment) => calculateLeverge(newAlignment)}
      >
        <ToggleButton value="tqqq">TQQQ</ToggleButton>
        <ToggleButton value="sqqq">SQQQ</ToggleButton>
      </ToggleButtonGroup>
    </>
  );
};

export default memo(ToggleTqqqSqqq);
