import { memo, useContext, useCallback } from 'react';
import { Button, Stack } from '@mui/material';

import { TABLE_RANGE } from 'constants/common';
import { calculateLeverageArray } from '../../MNQConverterHelpers';
import MNQConverterContext from '../../MNQConverterContext';

const CalculateButtonGroup = () => {
  const { toggleTqqqSqqq, setLeverageArray, nqValue, tqqqValue, sqqqValue, offsetValue } =
    useContext(MNQConverterContext);

  const calculateLeverge = useCallback(
    () =>
      setLeverageArray(
        calculateLeverageArray(
          toggleTqqqSqqq,
          nqValue,
          tqqqValue,
          sqqqValue,
          offsetValue,
          TABLE_RANGE
        )
      ),
    [setLeverageArray, nqValue, tqqqValue, sqqqValue, toggleTqqqSqqq, offsetValue]
  );

  return (
    <>
      <Stack sx={{ marginY: 2 }} direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 1, sm: 2 }}>
        <Button onClick={calculateLeverge} variant="contained" fullWidth>
          Calculate
        </Button>
      </Stack>
    </>
  );
};

export default memo(CalculateButtonGroup);
