import { createContext } from 'react';
import { ConversionValueType } from './MNQConverterHelpers';

export interface MNQConverterContextProps {
  readonly toggleTqqqSqqq: 'tqqq' | 'sqqq';
  readonly setToggleTqqqSqqq: (ticker: 'tqqq' | 'sqqq') => void;

  readonly nqValue?: number;
  readonly setNqValue: (value: number) => void;

  readonly tqqqValue?: number;
  readonly setTqqqValue: (value: number) => void;

  readonly sqqqValue?: number;
  readonly setSqqqValue: (value: number) => void;

  readonly stopLossValue?: number;
  readonly setStopLossValue: (value: number) => void;

  readonly averageDcaValue?: number;
  readonly setAverageDcaValue: (value: number) => void;

  readonly dcaQuantity?: number;
  readonly setDcaQuantity: (value: number) => void;

  readonly offsetValue?: number;
  readonly setOffsetValue: (value: number) => void;

  readonly leverageArray: ConversionValueType[];
  readonly setLeverageArray: (value: ConversionValueType[]) => void;

  readonly isLoading: boolean;
  readonly setIsLoading: (value: boolean) => void;

  readonly queryTime: Date;
  readonly setQueryTime: (value: Date) => void;
}

const MNQConverterContext = createContext<MNQConverterContextProps>({
  toggleTqqqSqqq: 'tqqq',
  setToggleTqqqSqqq: () => {},

  nqValue: 0,
  setNqValue: () => {},

  tqqqValue: 0,
  setTqqqValue: () => {},

  sqqqValue: 0,
  setSqqqValue: () => {},

  stopLossValue: 0,
  setStopLossValue: () => {},

  averageDcaValue: 0,
  setAverageDcaValue: () => {},

  dcaQuantity: 0,
  setDcaQuantity: () => {},

  offsetValue: 0,
  setOffsetValue: () => {},

  leverageArray: [],
  setLeverageArray: () => {},

  isLoading: true,
  setIsLoading: () => {},

  queryTime: new Date(),
  setQueryTime: () => {},
});

export default MNQConverterContext;
