import { memo, useEffect, useContext } from 'react';
import { Container, Grid, Typography, Divider, Box } from '@mui/material';

import { TABLE_RANGE, TABLE_OFFSET } from 'constants/common';
import { getData, writeData } from 'lib/firebase';

import Footer from 'components/Footer';
import LoadingScreen from 'components/LoadingScreen';

import MNQConverterContext from './MNQConverterContext';
import useMNQConverterProviderValues from './useMNQConverterProviderValues';

import ToggleTqqqSqqq from './components/ToggleTqqqSqqq';
import MarketPrices from './components/MarketPrices';
import StopLossPrice from './components/StopLossPrice';
import ConversionOffset from './components/ConversionOffset';
import CalculateButtonGroup from './components/CalculateButtonGroup';
import StopLossPriceConversion from './components/StopLossPriceConversion';
import ConversionTable from './components/ConversionTable';

import { getYahooFinanceData, calculateLeverageArray } from './MNQConverterHelpers';

const MNQConverterInner = () => {
  const { setIsLoading, setNqValue, setTqqqValue, setSqqqValue, setLeverageArray, setQueryTime } =
    useContext(MNQConverterContext);

  useEffect(() => {
    (async () => {
      const data = await getData();
      let nqPrice = data?.mnq || 0;
      let sqqqPrice = data?.sqqq || 0;
      let tqqqPrice = data?.tqqq || 0;
      let queryTime = data?.query_time?.seconds || 0;

      if (queryTime + 3600000 < new Date().getTime()) {
        const yahooFinanceData = await getYahooFinanceData();
        if (yahooFinanceData) {
          await writeData(yahooFinanceData);

          nqPrice = yahooFinanceData.mnq;
          sqqqPrice = yahooFinanceData.sqqq;
          tqqqPrice = yahooFinanceData.tqqq;
          queryTime = yahooFinanceData.query_time.seconds;
        }
      }

      setNqValue(nqPrice);
      setSqqqValue(sqqqPrice);
      setTqqqValue(tqqqPrice);
      setQueryTime(new Date(queryTime));
      setLeverageArray(
        calculateLeverageArray('tqqq', nqPrice, tqqqPrice, sqqqPrice, TABLE_OFFSET, TABLE_RANGE)
      );

      setIsLoading(false);
    })();
  }, [setIsLoading, setNqValue, setTqqqValue, setSqqqValue, setLeverageArray, setQueryTime]);

  return (
    <>
      <Grid container style={{ backgroundColor: 'white' }} paddingTop={4}>
        <Container>
          <Grid item xs={12}>
            <Typography sx={{ marginBottom: 4 }} variant="h2" component="h1">
              Simple MNQ Converter
            </Typography>
          </Grid>
          <Grid container direction={{ xs: 'column', md: 'row' }}>
            <Grid
              item
              p={2}
              xs={4}
              border={1}
              borderRadius={2}
              style={{ borderColor: 'lightgrey' }}
            >
              <ToggleTqqqSqqq />
              <Box component="form" noValidate autoComplete="off">
                <MarketPrices />
                <ConversionOffset />
                <CalculateButtonGroup />
              </Box>
              <Divider variant="middle" />
              <StopLossPrice />
              <StopLossPriceConversion />
            </Grid>
            <Grid item paddingX={{ xs: 0, md: 2 }} paddingY={{ xs: 2, md: 0 }} xs={8}>
              <ConversionTable />
            </Grid>
          </Grid>
        </Container>
        <Footer />
      </Grid>
      <LoadingScreen />
    </>
  );
};

const MNQConverter = () => {
  const mNQConverterProviderValues = useMNQConverterProviderValues();

  return (
    <MNQConverterContext.Provider value={mNQConverterProviderValues}>
      <MNQConverterInner />
    </MNQConverterContext.Provider>
  );
};

export default memo(MNQConverter);
