import { memo, useContext } from 'react';
import { FormControl, InputLabel, OutlinedInput, Typography } from '@mui/material';

import MNQConverterContext from '../../MNQConverterContext';

const StopLossPrice = () => {
  const {
    stopLossValue,
    setStopLossValue,
    averageDcaValue,
    setAverageDcaValue,
    dcaQuantity,
    setDcaQuantity,
    toggleTqqqSqqq,
  } = useContext(MNQConverterContext);

  return (
    <>
      <Typography variant="h6" sx={{ marginY: 2 }} gutterBottom>
        Stop loss calculation (optional)
      </Typography>
      <FormControl sx={{ marginBottom: 2 }} fullWidth>
        <InputLabel htmlFor="stop-value">Stop price (NQ)</InputLabel>
        <OutlinedInput
          id="stop-value"
          inputProps={{ inputMode: 'numeric', pattern: '[0-9]*.', min: 0 }}
          value={stopLossValue || ''}
          type="number"
          onChange={(e) => setStopLossValue(Number.parseFloat(e.target.value))}
          label="Stop price (NQ)"
          margin="dense"
        />
      </FormControl>
      <FormControl sx={{ marginBottom: 2 }} fullWidth>
        <InputLabel htmlFor="average-dca-value">
          Average DCA price ({toggleTqqqSqqq.toUpperCase()})
        </InputLabel>
        <OutlinedInput
          id="average-dca-value"
          inputProps={{ inputMode: 'numeric', pattern: '[0-9]*.', min: 0 }}
          value={averageDcaValue || ''}
          type="number"
          onChange={(e) => setAverageDcaValue(Number.parseFloat(e.target.value))}
          label={`Average DCA price (${toggleTqqqSqqq.toUpperCase()})`}
          margin="dense"
        />
      </FormControl>
      <FormControl sx={{ marginBottom: 2 }} fullWidth>
        <InputLabel htmlFor="dca-quantity">DCA quantity</InputLabel>
        <OutlinedInput
          id="dca-quantity"
          inputProps={{ inputMode: 'numeric', pattern: '[0-9]*.', min: 1 }}
          value={dcaQuantity || ''}
          type="number"
          onChange={(e) => setDcaQuantity(Number.parseFloat(e.target.value))}
          label="DCA quantity"
          margin="dense"
        />
      </FormControl>
    </>
  );
};

export default memo(StopLossPrice);
