import { memo, useContext, useState, useEffect } from 'react';
import { Typography, Grid } from '@mui/material';

import MNQConverterContext from '../../MNQConverterContext';
import { calculateLeverageValue } from '../../MNQConverterHelpers';

const StopLossPriceConversion = () => {
  const {
    stopLossValue,
    toggleTqqqSqqq,
    nqValue,
    tqqqValue,
    sqqqValue,
    dcaQuantity,
    averageDcaValue,
  } = useContext(MNQConverterContext);

  const [stopCalculatedValue, setStopCalculatedValue] = useState<string>();
  const [maxLossValue, setMaxLossValue] = useState<number>();

  useEffect(() => {
    if (stopLossValue && stopLossValue > 0) {
      const leverageValue = calculateLeverageValue(
        toggleTqqqSqqq === 'tqqq',
        nqValue || 0,
        stopLossValue || 0,
        (toggleTqqqSqqq === 'tqqq' ? tqqqValue : sqqqValue) || 0
      );
      setStopCalculatedValue(leverageValue.toFixed(2));

      setMaxLossValue((leverageValue - (averageDcaValue || leverageValue)) * (dcaQuantity || 1));
    }
  }, [
    toggleTqqqSqqq,
    stopLossValue,
    nqValue,
    tqqqValue,
    sqqqValue,
    dcaQuantity,
    setStopCalculatedValue,
    averageDcaValue,
  ]);

  return (
    <Grid container direction={{ xs: 'column', md: 'row' }} justifyContent="space-between">
      <Grid item>
        <Typography>Stop loss ({toggleTqqqSqqq.toUpperCase()})</Typography>
        <Typography variant="h5">{stopCalculatedValue || 'N/A'}</Typography>
      </Grid>
      <Grid item marginTop={{ xs: 1, md: 0 }}>
        <Typography
          textAlign={{ xs: 'left', md: 'right' }}
          color={!maxLossValue || maxLossValue >= 0 ? 'green' : 'red'}
          fontWeight="bold"
        >
          Max loss
        </Typography>
        <Typography
          textAlign={{ xs: 'left', md: 'right' }}
          variant="h5"
          color={!maxLossValue || maxLossValue >= 0 ? 'green' : 'red'}
          fontWeight="bold"
        >
          {maxLossValue?.toFixed(2) || 'N/A'}
        </Typography>
      </Grid>
    </Grid>
  );
};

export default memo(StopLossPriceConversion);
