import { memo, useContext } from 'react';
import { CircularProgress } from '@mui/material';

import MNQConverterContext from 'pages/MNQConverter/MNQConverterContext';

const LoadingScreen = () => {
  const { isLoading } = useContext(MNQConverterContext);
  if (!isLoading) return null;

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100vw',
        height: '100vh',
        backgroundColor: '#00000077',
        zIndex: 9999,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <CircularProgress />
    </div>
  );
};

export default memo(LoadingScreen);
