import { memo, useContext } from 'react';
import { FormControl, InputLabel, OutlinedInput, Typography } from '@mui/material';

import MNQConverterContext from '../../MNQConverterContext';

const MarketPrices = () => {
  const {
    toggleTqqqSqqq,
    nqValue,
    setNqValue,
    tqqqValue,
    setTqqqValue,
    sqqqValue,
    setSqqqValue,
    queryTime,
  } = useContext(MNQConverterContext);

  const isTqqq = toggleTqqqSqqq === 'tqqq';

  return (
    <>
      <Typography variant="h6" sx={{ marginTop: 2 }} gutterBottom>
        Set market data (MNQ=F)
      </Typography>
      <Typography variant="body2" sx={{ marginBottom: 2 }} gutterBottom>
        Last fetched: {queryTime.toLocaleString()}
      </Typography>
      <FormControl sx={{ marginBottom: 2 }} fullWidth>
        <InputLabel htmlFor="mnq-value">MNQ Price</InputLabel>
        <OutlinedInput
          id="mnq-value"
          inputProps={{ inputMode: 'numeric', pattern: '[0-9]*.' }}
          value={nqValue || ''}
          type="number"
          onChange={(e) => setNqValue(Number.parseFloat(e.target.value))}
          label="MNQ Price"
          margin="dense"
        />
      </FormControl>
      <FormControl fullWidth>
        <InputLabel htmlFor={`${isTqqq ? 'tqqq' : 'sqqq'}-value`}>
          {isTqqq ? 'TQQQ' : 'SQQQ'} Price
        </InputLabel>
        <OutlinedInput
          id={`${isTqqq ? 'tqqq' : 'sqqq'}-value`}
          inputProps={{ inputMode: 'numeric', pattern: '[0-9]*.' }}
          value={(isTqqq ? tqqqValue : sqqqValue) || ''}
          type="number"
          onChange={(e) =>
            isTqqq
              ? setTqqqValue(Number.parseFloat(e.target.value))
              : setSqqqValue(Number.parseFloat(e.target.value))
          }
          label={`${isTqqq ? 'TQQQ' : 'SQQQ'} Price`}
          margin="dense"
        />
      </FormControl>
    </>
  );
};

export default memo(MarketPrices);
