import axios, { AxiosResponse } from 'axios';
import { API_TIMEOUT } from 'constants/common';
import { requestAPI, Response } from './axiosHelper';

const TIMEOUT = 'Request timeout.';
const API_ERROR = 'API Error: "{0}"';
export const API_ERROR_UNDEFINED = 'API Error: "Internal server error"';

const apiHandlerCreator = async <ServerData>({
  type,
  url,
  params,
  customHeaders = {},
  additionalSettings = {},
}: {
  type: 'get' | 'post' | 'put' | 'patch' | 'delete';
  url: string;
  params?: Record<any, any>;
  customHeaders: Record<any, any>;
  additionalSettings: Record<any, any>;
}): Promise<AxiosResponse<ServerData> | undefined> => {
  let promiseTimeout;
  try {
    const timeout = new Promise<string>((r) => {
      promiseTimeout = setTimeout(() => r(TIMEOUT), API_TIMEOUT);
    });
    const axiosResult = params
      ? axios[type]<ServerData>(url, params, {
          headers: {
            ...customHeaders,
          },
          ...additionalSettings,
        })
      : axios[type]<ServerData>(url, {
          headers: {
            ...customHeaders,
          },
          ...additionalSettings,
        });

    const result = await Promise.race([timeout, axiosResult]);
    if (!result) {
      throw Error(API_ERROR_UNDEFINED);
    }

    if (typeof result === 'string') {
      throw Error(TIMEOUT);
    }

    clearTimeout(promiseTimeout);
    return result;
  } catch (error: any) {
    clearTimeout(promiseTimeout);
    throw Error(API_ERROR.replace('{0}', error.message));
  }
};

const GET = async <ServerData>(
  url: string,
  customHeaders: Record<any, any> = {},
  additionalSettings: Record<any, any> = {}
): Promise<Response<ServerData>> =>
  requestAPI(() => apiHandlerCreator({ type: 'get', url, customHeaders, additionalSettings }));
const DELETE = async <ServerData>(
  url: string,
  customHeaders: Record<any, any> = {},
  additionalSettings: Record<any, any> = {}
): Promise<Response<ServerData>> =>
  requestAPI(() => apiHandlerCreator({ type: 'delete', url, customHeaders, additionalSettings }));
const POST = async <ServerData>(
  url: string,
  params: Record<any, any> = {},
  customHeaders: Record<any, any> = {},
  additionalSettings: Record<any, any> = {}
): Promise<Response<ServerData>> =>
  requestAPI(() =>
    apiHandlerCreator({ type: 'post', url, params, customHeaders, additionalSettings })
  );
const PUT = async <ServerData>(
  url: string,
  params: Record<any, any> = {},
  customHeaders: Record<any, any> = {},
  additionalSettings: Record<any, any> = {}
): Promise<Response<ServerData>> =>
  requestAPI(() =>
    apiHandlerCreator({ type: 'put', url, params, customHeaders, additionalSettings })
  );
const PATCH = async <ServerData>(
  url: string,
  params: Record<any, any> = {},
  customHeaders: Record<any, any> = {},
  additionalSettings: Record<any, any> = {}
): Promise<Response<ServerData>> =>
  requestAPI(() =>
    apiHandlerCreator({ type: 'patch', url, params, customHeaders, additionalSettings })
  );

export default {
  GET,
  DELETE,
  POST,
  PUT,
  PATCH,
};
