import React, { memo, useContext, useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from '@mui/material';

import MNQConverterContext from '../../MNQConverterContext';

const ConversionTable = () => {
  const { leverageArray } = useContext(MNQConverterContext);
  const [scrollRef, setScrollRef] = useState<HTMLDivElement>();

  useEffect(() => {
    if (scrollRef) {
      setTimeout(() => {
        scrollRef.scrollTop = scrollRef.scrollHeight * (window.innerWidth < 900 ? 0.43 : 0.35);
      }, 10);
    }
  }, [scrollRef, leverageArray]);

  return (
    <TableContainer
      component={Paper}
      sx={{ maxHeight: { xs: 400, md: 770 } }}
      ref={(node: HTMLDivElement) => {
        if (!scrollRef && node) setScrollRef(node);
      }}
      id="table"
    >
      <Table stickyHeader aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Target</TableCell>
            <TableCell align="right">Price</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {leverageArray.map((row, index) => {
            const isActual = index === Math.floor(leverageArray.length * 0.5);
            return (
              <TableRow
                key={row.target}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                style={
                  isActual
                    ? {
                        backgroundColor: 'lightblue',
                      }
                    : {}
                }
                hover
              >
                <TableCell component="th" scope="row" style={{ paddingTop: 2, paddingBottom: 2 }}>
                  {row.target}
                </TableCell>
                <TableCell align="right" style={{ paddingTop: 2, paddingBottom: 2 }}>
                  {row.value}
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default memo(ConversionTable);
