import { memo } from 'react';
import { Grid, Container, Typography } from '@mui/material';

const Footer = () => {
  const year = new Date().getFullYear();

  return (
    <Grid item xs={12} marginTop={2} paddingY={2} maxHeight={200}>
      <Container>
        <Typography fontSize={12} gutterBottom>
          Conversions might have slight differences due to rounding. They will also be more accurate
          when nearer to the actual price.
        </Typography>
        <Typography fontSize={12}>
          Just trying to get rich. Special thanks to @yytellmewhy and @apegun.
        </Typography>
        <Typography fontSize={12}>v{process.env.REACT_APP_VERSION}</Typography>
        <Typography fontSize={12} gutterBottom>
          © {year} @kaiserbreathx
        </Typography>
      </Container>
    </Grid>
  );
};

export default memo(Footer);
