import { memo, useContext, useCallback } from 'react';
import { Grid, Slider, Input, Typography } from '@mui/material';

import MNQConverterContext from '../../MNQConverterContext';

const ConversionOffset = () => {
  const { offsetValue, setOffsetValue } = useContext(MNQConverterContext);

  const handleBlur = useCallback(() => {
    if (offsetValue && offsetValue < 0) {
      setOffsetValue(0);
    }
  }, [offsetValue, setOffsetValue]);

  return (
    <>
      <Typography sx={{ marginY: 2 }} gutterBottom>
        Interval (ticks)
      </Typography>
      <Grid container spacing={2} alignItems="center">
        <Grid item xs>
          <Slider
            value={typeof offsetValue === 'number' ? offsetValue : 0}
            onChange={(_e, value) => setOffsetValue(typeof value === 'number' ? value : 0)}
            aria-labelledby="input-slider"
          />
        </Grid>
        <Grid item>
          <Input
            value={offsetValue || ''}
            size="small"
            onChange={(e) => setOffsetValue(Number.parseInt(e.target.value))}
            onBlur={handleBlur}
            inputProps={{
              step: 10,
              min: 0,
              max: 100,
              type: 'number',
              'aria-labelledby': 'input-slider',
              inputMode: 'numeric',
              pattern: '[0-9]*',
            }}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default memo(ConversionOffset);
