import { useState, useMemo } from 'react';
import { MNQConverterContextProps } from './MNQConverterContext';
import { TABLE_OFFSET } from 'constants/common';

const useMNQConverterProviderValues = () => {
  const [toggleTqqqSqqq, setToggleTqqqSqqq] =
    useState<MNQConverterContextProps['toggleTqqqSqqq']>('tqqq');
  const [nqValue, setNqValue] = useState<MNQConverterContextProps['nqValue']>(0);
  const [tqqqValue, setTqqqValue] = useState<MNQConverterContextProps['tqqqValue']>(0);
  const [sqqqValue, setSqqqValue] = useState<MNQConverterContextProps['sqqqValue']>(0);
  const [stopLossValue, setStopLossValue] = useState<MNQConverterContextProps['stopLossValue']>(0);
  const [averageDcaValue, setAverageDcaValue] =
    useState<MNQConverterContextProps['averageDcaValue']>(0);
  const [dcaQuantity, setDcaQuantity] = useState<MNQConverterContextProps['dcaQuantity']>(1);
  const [offsetValue, setOffsetValue] =
    useState<MNQConverterContextProps['offsetValue']>(TABLE_OFFSET);
  const [leverageArray, setLeverageArray] = useState<MNQConverterContextProps['leverageArray']>([]);
  const [isLoading, setIsLoading] = useState<MNQConverterContextProps['isLoading']>(true);
  const [queryTime, setQueryTime] = useState<MNQConverterContextProps['queryTime']>(new Date());

  return useMemo(
    () => ({
      toggleTqqqSqqq,
      setToggleTqqqSqqq,
      nqValue,
      setNqValue,
      tqqqValue,
      setTqqqValue,
      sqqqValue,
      setSqqqValue,
      stopLossValue,
      setStopLossValue,
      averageDcaValue,
      setAverageDcaValue,
      dcaQuantity,
      setDcaQuantity,
      offsetValue,
      setOffsetValue,
      leverageArray,
      setLeverageArray,
      isLoading,
      setIsLoading,
      queryTime,
      setQueryTime,
    }),
    [
      toggleTqqqSqqq,
      setToggleTqqqSqqq,
      nqValue,
      setNqValue,
      tqqqValue,
      setTqqqValue,
      sqqqValue,
      setSqqqValue,
      stopLossValue,
      setStopLossValue,
      averageDcaValue,
      setAverageDcaValue,
      dcaQuantity,
      setDcaQuantity,
      offsetValue,
      setOffsetValue,
      leverageArray,
      setLeverageArray,
      isLoading,
      setIsLoading,
      queryTime,
      setQueryTime,
    ]
  );
};

export default useMNQConverterProviderValues;
